}
h1{
	font-family: san-serif;
}
img{
	width: 100%;
}
.information{
	position: relative;
	width: 100%;
	margin-top: 66px;
	display: flex;
	align-items: center;
	justify-content: center;
	resize: both;
}
.tag-line{
	font-size: 7rem;
	color: #fff;
	font-weight: bold;
	text-align: center;
}
.footer{
	display: flex;
	flex-direction: row;
	align-items: stretch;
	justify-content: space-around;
	padding: 32px 0;
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	background: #fff;
	box-shadow: -5px -5px 5px 5px #888;
	
}
.signup, .signin{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
}
input{
	margin-top: .5em;
	display: flex;
	flex-direction: column;
	align-items: center;
}
@media only screen and (min-width : 769px){
	.info{
		background: transparent;
	}
}
